body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  background-color:#64b5f6;
  line-height: 1.5rem;
  font-size: .875rem;
  color: #102a42;
}

*{
  margin: 0;
  padding: 0;
  box-sizing: border-box;
}
.container{
  width: 90vw;
  margin: 5rem 0;
  max-width: 450px;
  background: #fff;
  border-radius: .25rem;
  
  box-shadow: 0 5px 15px rgba(0, 0, 0, .4);
}
.user{
  display: grid;
  grid-template-columns: auto 1fr;
  grid-column-gap: 0.75em;
  align-items: center;
  padding: 1rem 2rem;
  cursor: pointer;
}
article.user:hover {
  background: #bbdefb;
}
.user img {
  width: 75px;
  height: 75px;
  object-fit: cover;
  border-radius: 50%;
  box-shadow: 0 5px 15px rgba(0, 0, 0, .1);
}
.user h4 {
  margin-bottom: .35rem;
}
p {
  margin-bottom:0;
  color: #617d98;
}
.main {
  min-height: 100vh;
  display: flex;
  justify-content: center;
  align-items: center;
}
h3{
  font-size: 2rem;
}
h4{
  font-size :1rem
}
.btn{
  background-color: #2196f3;
  border: 0;
  padding: .7rem 0;
  width: 90%;
  border-radius: 0.25rem;
  color: #fff;
  font-size: 1rem;
  letter-spacing: 0.2rem;
  cursor: pointer;
  transition: all .25s;
  margin: 1.5rem 5%;
}
.btn:hover{
  background-color: #1976d2;
}
h2{
  font-weight: 400;
  text-transform: none;
  font-size: 1.25rem;
  padding: 1rem 2rem;
}
.info{
  display: grid;
  grid-template-columns: auto auto;
  align-items: center;
}
.align-right {
  text-align: right;
}
button.remove {
  padding: 0.35rem 1rem;
  border: 0;
  border-radius: 0.2rem;
  background-color: #2196f3;
  color: #fff;
  transition: all .25s;
}

h1,
h2,
h3,
h4 {
  letter-spacing: .1rem;
  text-transform: capitalize;
  line-height: 1.25;
}
@media screen and (min-width:800px){
  h3 {
    font-size: 1.5rem;
  }

  h4 {
    font-size: 1rem
  }
}